import { ReactNode } from "react";
import { useAppContext } from "../../../AppContext";
import { SkillModel } from "../../../models/SkillModel";

export const HomeSkills = () => {
  const appProvider = useAppContext();
  const skills = appProvider!.skills;

  const skillComponents: ReactNode[] = [];
  skills?.map((e) => {
    skillComponents.push(<_Skills skill={e} />);
  });

  return (
    <div className="flex flex-col gap-16">
      <div className="text-xl font-semibold border border-0 border-b-4 border-gray pb-4">
        Skills
      </div>
      <div className="flex flex-col gap-8">{skillComponents}</div>
    </div>
  );
};

const _Skills = ({ skill }: { skill: SkillModel }) => {
  return (
    <div className="flex flex-col gap-4">
      <img className="w-16" src={skill.image} />
      <div className="font-semibold">{skill.name}</div>
      <div className="text-gray">{skill.description}</div>
    </div>
  );
};
