import { ReactNode } from "react";
import { useAppContext } from "../../../AppContext";
import { ExperienceModel } from "../../../models/ExperienceModel";

export const HomeExperiences = () => {
  const appProvider = useAppContext();
  const experiences = appProvider!.experiences;

  const experienceComponents: ReactNode[] = [];
  experiences?.map((e) => {
    experienceComponents.push(<_Experience skill={e} />);
  });

  return (
    <div className="flex flex-col gap-16">
      <div className="text-xl font-semibold border border-0 border-b-4 border-gray pb-4">
        Experiences
      </div>
      <div className="flex flex-col gap-8">{experienceComponents}</div>
    </div>
  );
};

const _Experience = ({ skill: experience }: { skill: ExperienceModel }) => {
  return (
    <div>
      <div className="font-semibold">{experience.position}</div>
      <div className="text-gray">{experience.company}</div>
      <div className="text-gray">{experience.startEnd}</div>
    </div>
  );
};
