import { ReactNode, useEffect, useState } from "react";
import { Link, useParams } from "react-router-dom";
import { useAppContext } from "../../AppContext";
import ImageViewer from "react-simple-image-viewer";
import ReactMarkdown from "react-markdown";

export const ProjectDetailPage = () => {
  const { id } = useParams();

  useEffect(() => {
    window.scrollTo({ top: 0, behavior: "auto" });
  }, [id]);

  const appProvider = useAppContext();
  const project = appProvider!.projects!.at(parseInt(id!));

  if (project === undefined) {
    window.location.href = "/";
    return <div></div>;
  }

  return (
    <div className="flex flex-col max-w-2xl m-auto p-4 md:p-16 gap-32">
      <div className="flex flex-col gap-4">
        <div className="flex gap-4 justify-between">
          <img className="w-24" src={project.thumbnail}></img>
          <Link to="/">
            <i className="fa-regular fa-circle-xmark text-2xl" />
          </Link>
        </div>
        <div className="text-2xl font-semibold">{project.title}</div>
        <div className="flex flex-col gap-4 text-gray">
          <ReactMarkdown>{project.description}</ReactMarkdown>
        </div>
      </div>
      <_ImageCarousel />
    </div>
  );
};

const _ImageCarousel = () => {
  const { id } = useParams();

  const appProvider = useAppContext();
  const project = appProvider!.projects!.at(parseInt(id!));

  const [currentIndex, setCurrentIndex] = useState(0);
  const [isImageViewerOpen, setIsImageViewerOpen] = useState(false);

  const images = project!.images;

  const openImageViewer = (index: number) => {
    setCurrentIndex(index);
    setIsImageViewerOpen(true);
  };

  const closeImageViewer = () => {
    setCurrentIndex(0);
    setIsImageViewerOpen(false);
  };

  const projectsImageComponents: ReactNode[] = [];
  for (let i = 0; i < project!.images.length; i++) {
    projectsImageComponents.push(
      <_ImageThumbnail
        image={project!.images[i]}
        onClick={() => openImageViewer(i)}
      />
    );
  }

  useEffect(() => {
    setCurrentIndex(0);
  }, [id]);

  return (
    <div className="flex flex-col gap-16">
      <div className="text-xl font-semibold border border-0 border-b-4 border-gray pb-4">
        Images
      </div>
      <div className="grid grid-cols-12 gap-4">{projectsImageComponents}</div>
      {isImageViewerOpen && (
        <ImageViewer
          src={images}
          currentIndex={currentIndex}
          disableScroll={false}
          closeOnClickOutside={true}
          onClose={closeImageViewer}
        />
      )}
    </div>
  );
};

const _ImageThumbnail = ({
  image,
  onClick,
}: {
  image: string;
  onClick: () => void;
}) => {
  return (
    <div
      className={
        "border-primary border rounded flex justify-center aspect-video bg-primary bg-cover bg-center bg-no-repeat cursor-pointer col-span-4 md:col-span-3 hover:opacity-75 transition duration-150 ease-in"
      }
      onClick={onClick}
      style={{ backgroundImage: "url(" + image + ")" }}
    />
  );
};
