import { ReactNode } from "react";
import { ReactMarkdown } from "react-markdown/lib/react-markdown";
import { Link } from "react-router-dom";
import { useAppContext } from "../../../AppContext";
import { ProjectModel } from "../../../models/ProjectModel";

export const HomeProjectList = () => {
  const appProvider = useAppContext();
  const projects = appProvider!.projects;

  const projectComponents: ReactNode[] = [];
  projects?.map((e, index) => {
    projectComponents.push(<_Project project={e} index={index} />);
  });

  return (
    <div className="flex flex-col gap-16">
      <div className="text-xl font-semibold border border-0 border-b-4 border-gray pb-4">
        Projects
      </div>
      <div className="flex flex-col gap-8">{projectComponents}</div>
    </div>
  );
};

const _Project = ({
  project,
  index,
}: {
  project: ProjectModel;
  index: number;
}) => {
  return (
    <Link
      to={"/project/" + index}
      className="flex flex-col gap-4 hover:opacity-75 transition duration-150 ease-in"
    >
      <img className="w-24" src={project.thumbnail}></img>
      <div className="font-semibold">{project.title}</div>
      <div className="flex flex-col gap-4 text-gray">
        <ReactMarkdown>{project.description}</ReactMarkdown>
      </div>
    </Link>
  );
};
